import './style.less'
import React from 'react';
import assign from 'object-assign'
import Page from '../../framework/page/Page';
import SearchCondition from '../search-condition/SearchCondition'
import InputItem from '../search-condition/InputItem'
class ListPage extends React.Component {
    state = {
        loading: false,
        conditionData: {},
        currentPage: 1,
        pageSize: 10
    };
    conditionData = {}

    componentDidMount() {

    }

    handleSearch = ()=> {
        console.log(this.conditionData);
    };

    render() {
        let _this = this;
        let options = {
            showSearchBtn: true,
            onSearch: (data)=> {
                _this.handleSearch();
            },
            conditionItems: [
                {
                    type: 'selectCascaded',
                    items: [
                        {
                            type: 'select',
                            name: 'select1',
                            label: '',
                            searchOnChange: true,
                            defaultValue: '4',
                            options: [
                                {value: 'all', text: ''},
                                {value: '1', text: ''},
                                {value: '2', text: ''},
                                {value: '3', text: ''},
                                {value: '4', text: ''}
                            ],
                            getNextOptions: function (value) {
                                return [
                                    {value: value + '-1', text: value + '-1 '},
                                    {value: value + '-2', text: value + '-2 '},
                                    {value: value + '-3', text: value + '-3 '},
                                    {value: value + '-4', text: value + '-4 '}
                                ]
                            }
                        },
                        {
                            type: 'selectSearch',
                            name: 'select2',
                            label: '',
                            width: 200,
                            searchOnChange: true,
                            getNextOptions: function (value) {
                                return [
                                    {value: value + '-1', text: value + '-1 '},
                                    {value: value + '-2', text: value + '-2 '},
                                    {value: value + '-3', text: value + '-3 '},
                                    {value: value + '-4', text: value + '-4 '}
                                ]
                            }
                        },
                        {
                            type: 'select',// 
                            name: 'select3',
                            label: '',
                            searchOnChange: true,
                            getNextOptions: function (value) {
                                return [
                                    {value: value + '-1', text: value + '-1 '},
                                    {value: value + '-2', text: value + '-2 '},
                                    {value: value + '-3', text: value + '-3 '},
                                    {value: value + '-4', text: value + '-4 '}
                                ]
                            }
                        },
                        {
                            type: 'select',
                            name: 'select4',
                            label: '',
                            searchOnChange: true
                        }
                    ]
                },
                {
                    name: 'store',
                    label: '',
                    type: 'input',
                    searchOnChange: true
                },
                {
                    name: 'combobox',
                    label: '',
                    type: 'combobox',
                    searchOnChange: true,
                    defaultValue: '',
                    separator: '@',
                    options: ['gmail.com', '163.com', 'qq.com']
                },

                [
                    {
                        type: 'select',
                        name: 'select',
                        label: '',
                        defaultValue: 'all',
                        options: [
                            {value: 'all', text: ''},
                            {value: '1', text: ''},
                            {value: '2', text: ''},
                            {value: '3', text: ''},
                            {value: '4', text: ''}
                        ]
                    },
                    {
                        type: 'selectSearch',
                        name: 'selectSearch',
                        label: '',
                        defaultValue: 'all',
                        options: [
                            {value: 'all', text: ''},
                            {value: '1', text: ''},
                            {value: '2', text: ''},
                            {value: '3', text: ''},
                            {value: '4', text: ''}
                        ]
                    }
                ],
                {
                    type: 'selectMultiple',
                    name: 'selectMultiple',
                    label: '',
                    width: 400,
                    defaultValue: ['all', '1', '2'],
                    options: [
                        {value: 'all', text: ''},
                        {value: '1', text: ''},
                        {value: '2', text: ''},
                        {value: '3', text: ''},
                        {value: '4', text: ''}
                    ]
                },
                {
                    type: 'radio',
                    name: 'radio',
                    label: '',
                    defaultValue: 'all',
                    options: [
                        {value: 'all', text: ''},
                        {value: '1', text: ''},
                        {value: '2', text: ''},
                        {value: '3', text: ''},
                        {value: '4', text: ''}
                    ]
                },
                {
                    type: 'radioButton',
                    name: 'radioButton',
                    label: '',
                    defaultValue: 'all',
                    options: [
                        {value: 'all', text: ''},
                        {value: '1', text: ''},
                        {value: '2', text: ''},
                        {value: '3', text: ''},
                        {value: '4', text: ''}
                    ]
                },
                {
                    type: 'checkbox',
                    name: 'checkbox',
                    label: '',
                    searchOnChange: true,
                    defaultValue: ['1', '5', '3'],
                    options: [
                        {value: '1', text: '1'},
                        {value: '2', text: '2'},
                        {value: '3', text: '3'},
                        {value: '4', text: '4'},
                        {value: '5', text: '5'}
                    ]
                },
                {
                    type: 'checkboxButton',
                    name: 'checkboxButton',
                    label: '',
                    searchOnChange: true,
                    defaultValue: ['22', '44'],
                    options: [
                        {value: '00', text: ''},
                        {value: '11', text: ''},
                        {value: '22', text: ''},
                        {value: '33', text: ''},
                        {value: '44', text: ''}
                    ]
                },
                [
                    {
                        type: 'date',
                        name: 'date',
                        label: '',
                        defaultValue: '2015-01-01'
                    },
                    {
                        type: 'dateArea',
                        name: 'dateArea',
                        label: '',
                        options: ['2016-06-06', '2016-07-07']
                    }
                ],
                [
                    {
                        type: 'time',
                        name: 'time',
                        label: '',
                        defaultValue: '11:51'
                    },
                    {
                        type: 'timeArea',
                        name: 'timeArea',
                        label: '',
                        width: 73,
                        options: ['11:11', '12:12']
                    }
                ],
                [
                    {
                        type: 'dateTime',
                        name: 'dateTime',
                        label: '',
                        defaultValue: '2016-01-01 11:51'
                    },
                    {
                        type: 'dateTimeArea',
                        name: 'dateTimeArea',
                        label: '',
                        options: ['2016-01-01 11:11', '2016-02-02 22:22']
                    }
                ],
                {
                    type: 'customer',
                    name: 'customer',
                    label: '',
                    component: InputItem
                }
            ]
        };
        return (
            <Page header="auto" loading={this.state.loading}>
                <SearchCondition options={options} setConditionData={(data)=>this.conditionData=data}/>
            </Page>
        );
    }
}
export default ListPage;



